import { store, persistor, RootState } from './app/store';
import { Provider, useSelector } from 'react-redux';
import { DarkTheme, DefaultTheme, NavigationContainer } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import { Calculator } from './screens/Calculator';
import { Settings } from './screens/Settings';
import { PersistGate } from 'redux-persist/integration/react';
import * as Sentry from 'sentry-expo';

Sentry.init({
  dsn: 'https://94d928b7a1c5d0fa9963c8b6c58ff15c@o4506534835322880.ingest.sentry.io/4506534838534144',
  enableInExpoDevelopment: false,
  debug: false,
});

function AppNavigation() {
  const Stack = createNativeStackNavigator();
  
  const theme = useSelector((state: RootState) => state.settings.theme);
  return <NavigationContainer theme={theme === 'dark' ? DarkTheme : DefaultTheme}>
    <Stack.Navigator initialRouteName="Home">
      <Stack.Screen
        name="Home"
        component={Calculator}
        options={{
          headerShown: false,
          title: 'Market Calculator'
        }}
      />
      <Stack.Screen
        name="Settings"
        component={Settings}
        options={{
          headerShown: false,
          title: 'Settings | Market Calculator'
        }}
      />
    </Stack.Navigator>
  </NavigationContainer>
};

export default function App() {
  return (
    <Provider store={store}>
      <PersistGate loading={null} persistor={persistor}>
        <AppNavigation />
      </PersistGate>
    </Provider>
  );
};
