import { createSlice } from '@reduxjs/toolkit'
import type { PayloadAction } from '@reduxjs/toolkit'

export interface SettingsState {
    theme: string,
    comma: string,
    currency: string,
    fractionDigits: number,
    timer: number,
};

const initialState: SettingsState = {
    theme: "dark",
    comma: ".",
    currency: "$",
    fractionDigits: 2,
    timer: 5,
};

export const settingsSlice = createSlice({
    name: 'settings',
    initialState,
    reducers: {
        changeTheme: (state, action: PayloadAction<string>) => {
            state.theme = action.payload;
        },
        changeComma: (state, action: PayloadAction<string>) => {
            state.comma = action.payload;
        },
        changeCurrency: (state, action: PayloadAction<string>) => {
            state.currency = action.payload;
        },
        incrementFractionDigits: (state) => {
            if (state.fractionDigits < 10) {
                state.fractionDigits += 1;
            }
        },
        decrementFractionDigits: (state) => {
            if (state.fractionDigits > 1) {
                state.fractionDigits -= 1;
            }
        },
        setFractionDigits: (state, action: PayloadAction<number>) => {
            if (action.payload >= 1 && action.payload <= 10) {
                state.fractionDigits = action.payload;
            }
        },
        incrementTimer: (state) => {
            state.timer += 1;
        },
        decrementTimer: (state) => {
            if (state.timer > 1) {
                state.timer -= 1;
            }
        },
        setTimerAmount: (state, action: PayloadAction<number>) => {
            state.timer = action.payload;
        },
    },
});

export const {
    changeTheme,
    changeComma,
    changeCurrency,
    incrementFractionDigits,
    decrementFractionDigits,
    setFractionDigits,
    incrementTimer,
    decrementTimer,
    setTimerAmount,
} = settingsSlice.actions;

export default settingsSlice.reducer;
