import {
    useState,
    useRef,
    useEffect,
    useCallback,
    useMemo
} from 'react';
import {
    Text,
    FlatList,
    View,
    Pressable,
    SafeAreaView
} from 'react-native';
import { styles } from '../styles';
import Ionicons from '@expo/vector-icons/Ionicons';
import Animated, {
    useSharedValue,
    useAnimatedStyle,
    withTiming
} from 'react-native-reanimated';
import { useSelector } from 'react-redux';
import { RootState } from '../app/store';

export const Calculator = ({ navigation }: { navigation: any }) => {
    const timer = useSelector((state: RootState) => state.settings.timer);
    const theme = useSelector((state: RootState) => state.settings.theme);
    const comma = useSelector((state: RootState) => state.settings.comma);
    const currency = useSelector((state: RootState) => state.settings.currency);
    const fractionDigits = useSelector((state: RootState) => state.settings.fractionDigits);

    const style = styles(theme as NonNullable<ColorSchemeName>);

    const timerRef = useRef(timer);
    const [timerActive, setTimerActive] = useState(false);
    const [timerDisplay, setTimerDisplay] = useState(timer);
    const [displayText, setDisplayText] = useState('0');
    const [total, setTotal] = useState(0);
    const [nextId, setNextId] = useState(0);
    const [receipt, setReceipt] = useState<ItemProps[]>([]);
    const [pressableWidth, setPressableWidth] = useState(0);
    const progressAnimRef = useSharedValue(0);

    const animateProgress = () => {
        progressAnimRef.value = 0;
        progressAnimRef.value = withTiming(1, { duration: timer * 1000 });
    };

    const cancelTimer = () => {
        timerRef.current = timer;
        setTimerDisplay(timerRef.current);
        setTimerActive(false);
        progressAnimRef.value = 0;
    };

    const progressStyle = useAnimatedStyle(() => {
        return {
            width: progressAnimRef.value * pressableWidth,
        };
    });

    const numberDisplay = (num: number) => {
        return num.toFixed(fractionDigits).replace('.', comma);
    };

    const handleNumberInput = (num: number) => {
        if (displayText === '0') {
            setDisplayText(num.toString());
            if (num !== 0) {
                setTimerActive(true);
                animateProgress();
            }
        } else {
            setDisplayText(displayText + num.toString());
            setTimerActive(true);
            animateProgress();
            timerRef.current = timer;
            setTimerDisplay(timerRef.current);
        }
    };

    const handleDelete = () => {
        if (displayText !== '0') {
            if (displayText.length > 1) {
                setDisplayText(displayText.substring(0, displayText.length - 1));
                animateProgress();
                timerRef.current = timer;
                setTimerDisplay(timerRef.current);
            } else {
                handleClearEntry();
            }
        }
    };

    const handleComma = () => {
        if (displayText !== '0') {
            animateProgress();
            timerRef.current = timer;
            setTimerDisplay(timerRef.current);
        }
        setDisplayText(displayText + comma);
    };

    const handleAdd = () => {
        if (displayText === '0') {
            return;
        }
        let receiptItem = {
            id: nextId,
            price: parseFloat(displayText.replace(comma, '.')),
            count: 1
        };
        setReceipt([
            receiptItem,
            ...receipt,
        ]);
        setTotal(total + parseFloat(displayText.replace(comma, '.')));
        setNextId(nextId + 1);
        handleClearEntry();
    };

    const handleClear = () => {
        setTotal(0);
        setReceipt([]);
        handleClearEntry();
    };

    const handleClearEntry = () => {
        setDisplayText('0');
        cancelTimer();
    };

    useEffect(() => {
        const timerId = setInterval(() => {
            if (timerActive) {
                if (timerRef.current >= 0) {
                    timerRef.current -= 1;
                    setTimerDisplay(timerRef.current);
                }
                if (timerRef.current == 0) {
                    if (displayText !== '0') {
                        handleAdd();
                        setTimerActive(false);
                        timerRef.current = timer;
                        setTimerDisplay(timerRef.current);
                    }
                    clearInterval(timerId);
                }
            } else {
                clearInterval(timerId);
            }
        }, 1000);
        return () => {
            clearInterval(timerId);
        };
    }, [timerActive, displayText]);
    useEffect(() => {
        timerRef.current = timer;
        setTimerDisplay(timer);
    }, [timer]);

    const handlePressMinus = useCallback((id: number, price: number, count: number) => {
        setTotal(total - price);
        if (count === 1) {
            setReceipt(
                receipt.filter(a =>
                    a.id !== id
                )
            );
        } else {
            const nextReceipt = receipt.map(receiptItem => {
                if (receiptItem.id === id) {
                    return { ...receiptItem, count: count - 1 };
                } else {
                    return receiptItem;
                }
            });
            setReceipt(nextReceipt);
        }
    }, [receipt, total]);

    const handlePressPlus = useCallback((id: number, price: number, count: number) => {
        const nextReceipt = receipt.map(receiptItem => {
            if (receiptItem.id === id) {
                setTotal(total + price);
                return { ...receiptItem, count: count + 1 };
            } else {
                return receiptItem;
            }
        });
        setReceipt(nextReceipt);
    }, [receipt, total]);

    const Item = useMemo(() => {
        return ({ id, price, count }: { id: number, price: number, count: number }) => (
            <View style={style.listItem}>
                <Pressable
                    style={style.listButton}
                    onPress={() => handlePressMinus(id, price, count)}
                >
                    <Text style={style.buttonText}>-</Text>
                </Pressable>
                <Text style={style.listText}>{numberDisplay(price)}</Text>
                <Text style={style.listText}>x{count}</Text>
                <Text style={style.listText}>=</Text>
                <Text style={style.listText}>{numberDisplay(price * count)}</Text>
                <Pressable
                    style={style.listButton}
                    onPress={() => handlePressPlus(id, price, count)}
                >
                    <Text style={style.buttonText}>+</Text>
                </Pressable>
            </View>
        );
    }, [receipt, total]);

    const ListFooter = () => {
        return <Text style={style.totalDisplay}>
            Total: {currency + ' ' + numberDisplay(total)}
        </Text>
    };
    return <SafeAreaView style={style.safeContainer}>
        <View style={style.container}>
            <View style={{ width: `100%`, height: 300, overflow: 'hidden' }}>
                <FlatList
                    style={style.listContainer}
                    data={receipt}
                    renderItem={
                        ({ item }) =>
                            <Item id={item.id} price={item.price} count={item.count} />
                    }
                    scrollEnabled={true}
                    inverted={true}
                    keyExtractor={(item) => item.id.toString()}
                />
            </View>
            <ListFooter />
            <View style={style.displayContainer}>
                <Text style={style.displayText}>
                    {currency + ' ' + displayText}
                </Text>
            </View><View style={style.buttonContainer}>
                <View style={style.row}>
                    <Pressable
                        style={style.button}
                        onPress={() => handleNumberInput(7)}
                    >
                        <Text style={style.buttonText}>7</Text>
                    </Pressable>
                    <Pressable
                        style={style.button}
                        onPress={() => handleNumberInput(8)}
                    >
                        <Text style={style.buttonText}>8</Text>
                    </Pressable>
                    <Pressable
                        style={style.button}
                        onPress={() => handleNumberInput(9)}
                    >
                        <Text style={style.buttonText}>9</Text>
                    </Pressable>
                    <Pressable
                        style={[style.button, style.operatorButton]}
                        onPress={handleDelete}
                    >
                        <Text style={[
                            style.buttonText,
                            style.operatorButtonText
                        ]}>
                            {'<'}
                        </Text>
                    </Pressable>
                </View>
                <View style={style.row}>
                    <Pressable
                        style={style.button}
                        onPress={() => handleNumberInput(4)}
                    >
                        <Text style={style.buttonText}>4</Text>
                    </Pressable>
                    <Pressable
                        style={style.button}
                        onPress={() => handleNumberInput(5)}
                    >
                        <Text style={style.buttonText}>5</Text>
                    </Pressable>
                    <Pressable
                        style={style.button}
                        onPress={() => handleNumberInput(6)}
                    >
                        <Text style={style.buttonText}>6</Text>
                    </Pressable>
                    <Pressable
                        style={[style.button, style.operatorButton]}
                        onPress={handleClearEntry}
                    >
                        <Text style={[
                            style.buttonText,
                            style.operatorButtonText
                        ]}>
                            CE
                        </Text>
                    </Pressable>
                </View>
                <View style={style.row}>
                    <Pressable
                        style={style.button}
                        onPress={() => handleNumberInput(1)}
                    >
                        <Text style={style.buttonText}>1</Text>
                    </Pressable>
                    <Pressable
                        style={style.button}
                        onPress={() => handleNumberInput(2)}
                    >
                        <Text style={style.buttonText}>2</Text>
                    </Pressable>
                    <Pressable
                        style={style.button}
                        onPress={() => handleNumberInput(3)}
                    >
                        <Text style={style.buttonText}>3</Text>
                    </Pressable>
                    <Pressable
                        style={[style.button, style.operatorButton]}
                        onPress={handleClear}
                    >
                        <Text style={[
                            style.buttonText,
                            style.operatorButtonText
                        ]}>
                            C
                        </Text>
                    </Pressable>
                </View>
                <View style={style.row}>
                    <Pressable
                        style={[style.button, style.operatorButton]}
                        onPress={() => { navigation.push('Settings') }}
                    >
                        <Ionicons name="md-settings" style={style.settingsIcon} />
                    </Pressable>
                    <Pressable
                        style={[style.button, style.zeroButton]}
                        onPress={() => handleNumberInput(0)}
                    >
                        <Text style={[
                            style.buttonText,
                            style.zeroButtonText
                        ]}>
                            0
                        </Text>
                    </Pressable>
                    <Pressable
                        style={[style.button, style.operatorButton]}
                        onPress={handleComma}
                    >
                        <Text style={[
                            style.buttonText,
                            style.operatorButtonText
                        ]}>
                            {comma}
                        </Text>
                    </Pressable>
                    <Pressable
                        style={[style.button, style.equalButton]}
                        onPress={handleAdd}
                    >
                        <Text style={style.equalButtonText}>
                            +
                        </Text>
                    </Pressable>
                </View>
                <Pressable
                    style={[style.button, style.clearButton]}
                    onLayout={event => {
                        setPressableWidth(event.nativeEvent.layout.width);
                    }}
                    onPress={cancelTimer}>
                    <Animated.View style={[style.animatedView, progressStyle]} />
                    <Text style={style.clearButtonText}>
                        Cancel
                    </Text>
                    <Text style={style.clearButtonText}>
                        {timerActive ? timerDisplay - 1 : ''}
                    </Text>
                </Pressable>
            </View>
        </View>
    </SafeAreaView>
};

