import React,
{
    useState,
    useCallback
} from 'react';
import {
    View,
    Text,
    Pressable,
    SafeAreaView,
    TextInput
} from 'react-native';
import DropDownPicker from 'react-native-dropdown-picker';
import { styles } from '../styles';
import currenciesList from '../constants/currencies';
import themesList from '../constants/themes';
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from '../app/store';
import {
    changeTheme,
    changeComma,
    changeCurrency,
    incrementFractionDigits,
    decrementFractionDigits,
    setFractionDigits,
    decrementTimer,
    incrementTimer,
    setTimerAmount
} from '../features/settings/settingsSlice';
import { MaterialIcons } from '@expo/vector-icons';

export const Settings = ({ navigation }: { navigation: any }) => {
    const theme = useSelector((state: RootState) => state.settings.theme);
    const comma = useSelector((state: RootState) => state.settings.comma);
    const currency = useSelector((state: RootState) => state.settings.currency);
    const fractionDigits = useSelector((state: RootState) => state.settings.fractionDigits);
    const timer = useSelector((state: RootState) => state.settings.timer);
    const dispatch = useDispatch();

    const style = styles(theme as NonNullable<ColorSchemeName>);

    const [currencyOpen, setCurrencyOpen] = useState(false);
    const [themeOpen, setThemeOpen] = useState(false);
    const [commaOpen, setCommaOpen] = useState(false);
    const onCurrenciesOpen = useCallback(() => {
        setThemeOpen(false);
        setCommaOpen(false);
    }, []);
    const onThemesOpen = useCallback(() => {
        setCurrencyOpen(false);
        setCommaOpen(false);
    }, []);
    const onCommasOpen = useCallback(() => {
        setCurrencyOpen(false);
        setThemeOpen(false);
    }, []);

    return (
        <SafeAreaView style={style.safeContainer}>
            <View style={style.settingsContainer}>
                <Pressable
                    style={style.settingsBackButton}
                    onPress={() => { navigation.goBack() }}
                >
                    <MaterialIcons name="arrow-back" style={style.settingsBackIcon} />
                </Pressable>

                <View style={[style.settingsRow, { zIndex: 5 }]}>
                    <Text style={style.settingsText}>Theme</Text>
                    <DropDownPicker
                        textStyle={style.settingsDropdownText}
                        open={themeOpen}
                        onOpen={onThemesOpen}
                        value={theme}
                        items={themesList}
                        setOpen={setThemeOpen}
                        setValue={() => { }}
                        onSelectItem={async (item) => {
                            if (item.value !== undefined) {
                                dispatch(changeTheme(item.value.toString()));
                            }
                        }}
                        theme={theme === 'light' ? 'LIGHT' : 'DARK'}
                    />
                </View>
                <View style={[style.settingsRow, { zIndex: 4 }]}>
                    <Text style={style.settingsText}>Comma</Text>
                    <DropDownPicker
                        textStyle={style.settingsDropdownText}
                        open={commaOpen}
                        onOpen={onCommasOpen}
                        value={comma}
                        items={[
                            { label: '. Dot', value: '.' },
                            { label: ', Comma', value: ',' },
                        ]}
                        setOpen={setCommaOpen}
                        setValue={() => { }}
                        theme={theme === 'light' ? 'LIGHT' : 'DARK'}
                        onSelectItem={async (item) => {
                            if (item.value !== undefined) {
                                dispatch(changeComma(item.value.toString()));
                            }
                        }}
                    />
                </View>
                <View style={[style.settingsRow, { zIndex: 3 }]}>
                    <Text style={style.settingsText}>Currency</Text>
                    <DropDownPicker
                        textStyle={style.settingsDropdownText}
                        open={currencyOpen}
                        onOpen={onCurrenciesOpen}
                        value={currency}
                        items={currenciesList}
                        setOpen={setCurrencyOpen}
                        setValue={() => { }}
                        theme={theme === 'light' ? 'LIGHT' : 'DARK'}
                        onSelectItem={async (item) => {
                            if (item.value !== undefined) {
                                dispatch(changeCurrency(item.value.toString()));
                            }
                        }}
                        searchable={true}
                    />
                </View>
                <View style={[style.settingsRow, { zIndex: 2 }]}>
                    <Text style={style.settingsText}>Timer</Text>
                    <View style={style.settingsInputRow}>
                        <Pressable
                            style={style.changeButton}
                            onPress={() => {
                                if (timer > 1) {
                                    dispatch(decrementTimer());
                                }
                            }}
                        >
                            <Text style={style.buttonText}>-</Text>
                        </Pressable>
                        <TextInput
                            value={timer.toString()}
                            style={style.textInput}
                            onChangeText={async text => {
                                if (!isNaN(Number(text)) && text !== '0') {
                                    dispatch(setTimerAmount(Number(text)));
                                }
                            }}
                            keyboardType='numeric'
                            inputMode='numeric'
                            maxLength={4}
                            clearTextOnFocus={true}
                            textAlign='center'
                        />
                        <Pressable
                            style={style.changeButton}
                            onPress={() => {
                                dispatch(incrementTimer());
                            }}
                        >
                            <Text style={style.buttonText}>+</Text>
                        </Pressable>
                    </View>
                </View>

                <View style={[style.settingsRow, { zIndex: 1 }]}>
                    <Text style={style.settingsText}>Fraction digits</Text>
                    <View style={style.settingsInputRow}>
                        <Pressable
                            style={style.changeButton}
                            onPress={() => {
                                dispatch(decrementFractionDigits());
                            }}
                        >
                            <Text style={style.buttonText}>-</Text>
                        </Pressable>
                        <TextInput
                            value={fractionDigits.toString()}
                            style={style.textInput}
                            onChangeText={async text => {
                                if (!isNaN(Number(text)) && text !== '0') {
                                    dispatch(setFractionDigits(Number(text)));
                                }
                            }}
                            keyboardType='numeric'
                            inputMode='numeric'
                            maxLength={4}
                            clearTextOnFocus={true}
                            textAlign='center'
                        />
                        <Pressable
                            style={style.changeButton}
                            onPress={() => {
                                dispatch(incrementFractionDigits());
                            }}
                        >
                            <Text style={style.buttonText}>+</Text>
                        </Pressable>
                    </View>
                </View>
            </View>
        </SafeAreaView>
    );
};
