import { StyleProp, TextStyle, ViewStyle } from 'react-native';
import colors from './colors';


function styles(colorScheme: NonNullable<ColorSchemeName>) {
    const safeContainer: StyleProp<ViewStyle> = {
        flex: 1,
        backgroundColor: colors[colorScheme].background,
    };
    const container: StyleProp<ViewStyle> = {
        flex: 1,
        margin: 20,
        alignItems: 'center',
        justifyContent: 'center',
    };
    const button: StyleProp<ViewStyle> = {
        flex: 1,
        borderRadius: 20,
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: colors[colorScheme].buttonBackground,
        margin: 5,
    };
    const buttonText: StyleProp<TextStyle> = {
        fontSize: 34,
        color: colors[colorScheme].textColor,
        textAlign: 'center',
    };
    const settingsIcon: StyleProp<TextStyle> = {
        fontSize: 32,
        color: colors[colorScheme].tint,
    };
    const zeroButton: StyleProp<ViewStyle> = {
        flex: 1,
    };
    const zeroButtonText: StyleProp<TextStyle> = {
        marginLeft: 10,
    };
    const operatorButton: StyleProp<ViewStyle> = {
        backgroundColor: colors[colorScheme].operatorButtonBackground,
    };
    const operatorButtonText: StyleProp<TextStyle> = {
        color: colors[colorScheme].tint,
    };
    const equalButton: StyleProp<ViewStyle> = {
        backgroundColor: colors[colorScheme].tint,
    };
    const equalButtonText: StyleProp<TextStyle> = {
        fontSize: 32,
        color: colors[colorScheme].textColor,
    };
    const clearButton: StyleProp<ViewStyle> = {
        backgroundColor: colors[colorScheme].operatorButtonBackground,
    };
    const clearButtonText: StyleProp<TextStyle> = {
        fontSize: 24,
        color: colors[colorScheme].textColor,
    };
    const listContainer: StyleProp<ViewStyle> = {
        flex: 1,
        width: "100%",
        height: 100,
        backgroundColor: colors[colorScheme].receiptBackground,
        borderRadius: 20
    };
    const listButton: StyleProp<ViewStyle> = {
        flex: 1,
        borderRadius: 20,
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: colors[colorScheme].buttonBackground,
        margin: 5,
        maxHeight: 75,
        maxWidth: 75,
        aspectRatio: 1,
    };
    const listText: StyleProp<TextStyle> = {
        flex: 1,
        fontSize: 24,
        textAlign: 'center',
        color: colors[colorScheme].textColor,
    };
    const totalDisplay: StyleProp<TextStyle> = {
        fontSize: 34,
        color: colors[colorScheme].textColor,
    };
    const listItem: StyleProp<ViewStyle> = {
        flex: 1,
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'center'
    };
    const displayContainer: StyleProp<ViewStyle> = {
        justifyContent: 'flex-end',
        alignItems: 'flex-end',
    };
    const displayText: StyleProp<TextStyle> = {
        fontSize: 48,
        color: colors[colorScheme].textColor,
    };
    const buttonContainer: StyleProp<ViewStyle> = {
        flex: 3,
        width: '100%',
        height: '50%'
    };
    const row: StyleProp<ViewStyle> = {
        flex: 1,
        flexDirection: 'row',
        justifyContent: 'space-between',
        marginBottom: 10,
    };
    const animatedView: StyleProp<ViewStyle> = {
        backgroundColor: colors[colorScheme].progressBar,
        position: 'absolute',
        borderRadius: 20,
        top: 0,
        left: 0,
        bottom: 0,
        right: 0
    };
    const backButton: StyleProp<ViewStyle> = {
        margin: 20,
        alignItems: 'center',
        justifyContent: 'center',
        height: 10
    };
    const textInput: StyleProp<TextStyle> = {
        color: colors[colorScheme].textColor,
        textAlign: 'center',
        fontSize: 30,
        width: 100,
    };
    const settingsText: StyleProp<TextStyle> = {
        fontSize: 34,
        color: colors[colorScheme].textColor,
        marginBottom: 10,
    };
    const settingsDropdownText: StyleProp<TextStyle> = {
        textAlign: 'center',
        fontSize: 20,
        color: colors[colorScheme].textColor,
    };
    const settingsContainer: StyleProp<ViewStyle> = {
        flex: 1,
        margin: 20,
        alignItems: 'center',
        justifyContent: 'center',
        flexDirection: 'column',
    };
    const settingsRow: StyleProp<ViewStyle> = {
        flex: 1,
        width: '100%',
        alignItems: 'center',
        height: 100,
    };
    const settingsInputRow: StyleProp<ViewStyle> = {
        flex: 1,
        flexDirection: 'row',
        alignItems: 'center',
        marginBottom: 10,
    };
    const changeButton: StyleProp<ViewStyle> = {
        flex: 1,
        borderRadius: 20,
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: colors[colorScheme].buttonBackground,
        maxHeight: 75,
        maxWidth: 75,
        minHeight: 50,
        minWidth: 50,
        aspectRatio: 1,
    };
    const settingsBackIcon: StyleProp<TextStyle> = {
        fontSize: 32,
        color: colors[colorScheme].tint,
    };
    const settingsBackButton: StyleProp<ViewStyle> = {
        flex: 1,
        borderRadius: 20,
        maxHeight: 75,
        maxWidth: 75,
        aspectRatio: 1,
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: colors[colorScheme].buttonBackground,
        margin: 5,
    };

    return {
        safeContainer: safeContainer,
        container: container,
        listContainer: listContainer,
        listButton: listButton,
        listItem: listItem,
        listText: listText,
        totalDisplay: totalDisplay,
        displayContainer: displayContainer,
        displayText: displayText,
        buttonContainer: buttonContainer,
        row: row,
        button: button,
        buttonText: buttonText,
        settingsIcon: settingsIcon,
        zeroButton: zeroButton,
        zeroButtonText: zeroButtonText,
        operatorButton: operatorButton,
        operatorButtonText: operatorButtonText,
        equalButton: equalButton,
        equalButtonText: equalButtonText,
        clearButton: clearButton,
        clearButtonText: clearButtonText,
        animatedView: animatedView,
        backButton: backButton,
        textInput: textInput,
        settingsText: settingsText,
        settingsDropdownText: settingsDropdownText,
        settingsContainer: settingsContainer,
        settingsRow: settingsRow,
        settingsInputRow: settingsInputRow,
        changeButton: changeButton,
        settingsBackIcon: settingsBackIcon,
        settingsBackButton: settingsBackButton,
    };
}

export { styles };
